.title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3%;
  margin-top: 1%;
  margin-bottom: 1%;
}

.box {
  border: 2px solid black;
  height: 500px;
  width: 94%;
  margin-left: 3%;
  margin-right: 3%;
  display: flex;
}

.video-container {
  width: 40%;
  height: 500px;
}

.video-container img {
  width: 100%;
  height: 100%;
}

.buttons-container {
  width: 60%;
  height: 500px;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  width: 100%;
  height: 100%;
}

button {
  margin: 5px;
}